/* globals window, document, CS_CONFIG, EditorWatchdog, ClassicEditor, LICENSE_KEY */

// Application data will be available under a global variable `appData`.
const appData = {
	// Users data.
	users: [
		{
			id: 'user-1',
			name: 'Mex Haddox'
		},
		{
			id: 'user-2',
			name: 'Zee Croce'
		}
	],

	// The ID of the current user.
	userId: 'user-1'
};

class CommentsAndTrackChangesAdapter {
	constructor( editor ) {
		this.editor = editor;
	}

	init() {
		const usersPlugin = this.editor.plugins.get( 'Users' );
		const commentsRepositoryPlugin = this.editor.plugins.get( 'CommentsRepository' );
		const trackChangesPlugin = this.editor.plugins.get( 'TrackChanges' );

		// Load the users data.
		for ( const user of appData.users ) {
			usersPlugin.addUser( user );
		}

		// Set the current user.
		usersPlugin.defineMe( 'user-1' );

		commentsRepositoryPlugin.adapter = {
			addComment: () => Promise.resolve(),
			updateComment: () => Promise.resolve(),
			removeComment: () => Promise.resolve(),

			getCommentThread() {
				const initialCommentThreadData = {
					threadId: 'thread-1',
					comments: [
						{
							commentId: 'comment-1',
							authorId: 'user-2',
							content: '<p>Are we sure we want to use a made-up disorder name?</p>',
							createdAt: new Date()
						}
					]
				};

				return Promise.resolve( initialCommentThreadData );
			},
			addCommentThread: () => Promise.resolve( { comments: [] } ),
			updateCommentThread: () => Promise.resolve(),
			resolveCommentThread: () => Promise.resolve( { resolvedAt: new Date(), resolvedBy: usersPlugin.me.id } ),
			reopenCommentThread: () => Promise.resolve(),
			removeCommentThread: () => Promise.resolve()
		};

		// Set the adapter to the `TrackChanges#adapter` property.
		trackChangesPlugin.adapter = {
			getSuggestion: suggestionId => {
				return new Promise( resolve => {
					switch ( suggestionId ) {
						case 'suggestion-1':
							resolve( {
								id: 'suggestion-1',
								type: 'insertion',
								authorId: 'user-2',
								createdAt: new Date( 2019, 1, 13, 11, 20, 48 ),
								data: null,
								attributes: {}
							} );

							break;
						case 'suggestion-2':
							resolve( {
								id: 'suggestion-2',
								type: 'deletion',
								authorId: 'user-1',
								createdAt: new Date( 2019, 1, 14, 12, 7, 20 ),
								data: null,
								attributes: {}
							} );

							break;
						case 'suggestion-3': {
							const data = {
								commandName: 'bold',
								commandParams: [ { forceValue: true } ]
							};

							resolve( {
								id: 'suggestion-3',
								type: 'formatInline:886cqig6g8rf',
								authorId: 'user-1',
								createdAt: new Date( 2019, 2, 8, 10, 2, 7 ),
								data,
								attributes: {}
							} );

							break;
						}
					}
				} );
			},

			addSuggestion: () => {
				return Promise.resolve( {
					createdAt: new Date()
				} );
			},

			updateSuggestion: () => {
				return Promise.resolve();
			}
		};
	}
}

const initialData =
	`<h2>
		<comment-start name="thread-1"></comment-start>
		Bilingual Personality Disorder
		<comment-end name="thread-1"></comment-end>
	</h2>
	<p>
		This may be the first time you hear about this
		<suggestion-start name="insertion:suggestion-1:user-2"></suggestion-start>
		 made-up<suggestion-end name="insertion:suggestion-1:user-2"></suggestion-end>
		 disorder but it actually isn’t so far from the truth.
		As recent studies show, the language you speak has more effects on you than you realize.
		According to the studies, the language a person speaks affects their cognition,
		<suggestion-start name="deletion:suggestion-2:user-1"></suggestion-start>
		feelings, <suggestion-end name="deletion:suggestion-2:user-1"></suggestion-end>
		behavior, emotions and hence <strong>their personality</strong>.
	</p>
	<figure class="image image-style-side">
		<img src="../../../assets/img/collaboration-demo-img.jpg"
			srcset="../../../assets/img/collaboration-demo-img.jpg, ../../../assets/img/collaboration-demo-img_2x.jpg 2x">
		<figcaption>
			One language, one person.
		</figcaption>
	</figure>
	<p>
		This shouldn’t come as a surprise
		<a href="https://en.wikipedia.org/wiki/Lateralization_of_brain_function">since we already know</a>
		that different regions of the brain become more active depending on the activity.
		The structure, information and especially
		<suggestion-start name="formatInline:886cqig6g8rf:suggestion-3:user-1"></suggestion-start>
		the culture of languages<suggestion-end name="formatInline:886cqig6g8rf:suggestion-3:user-1"></suggestion-end>
		varies substantially
		and the language a person speaks is an essential element of daily life.
	</p>`;

const watchdog = new EditorWatchdog( ClassicEditor );
window.watchdog = watchdog;

watchdog.setCreator( ( el, config ) => {
	return ClassicEditor.create( el, config )
		.then( editor => {
			const annotationsUIs = editor.plugins.get( 'AnnotationsUIs' );

			const editorElement = document.querySelector( '.live-snippet .ck.ck-editor' );
			const sidebarElement = document.querySelector( '.sidebar' );
			const inlineButton = document.querySelector( '#inline' );
			const narrowButton = document.querySelector( '#narrow' );
			const wideButton = document.querySelector( '#wide' );
			const wideAndInlineButton = document.querySelector( '#wide-inline' );

			function markActiveButton( button ) {
				[ inlineButton, narrowButton, wideButton, wideAndInlineButton ]
					.forEach( el => el.classList.toggle( 'active', el === button ) );
			}

			function switchToInline() {
				markActiveButton( inlineButton );
				sidebarElement.classList.remove( 'narrow' );
				sidebarElement.classList.add( 'hidden' );
				annotationsUIs.switchTo( 'inline' );

				editorElement.classList.remove( 'custom-sidebar-wide' );
				editorElement.classList.remove( 'custom-sidebar-narrow' );
			}

			function switchToNarrowSidebar() {
				markActiveButton( narrowButton );
				sidebarElement.classList.remove( 'hidden' );
				sidebarElement.classList.add( 'narrow' );
				annotationsUIs.switchTo( 'narrowSidebar' );

				editorElement.classList.remove( 'custom-sidebar-wide' );
				editorElement.classList.add( 'custom-sidebar-narrow' );
			}

			function switchToWideSidebar() {
				markActiveButton( wideButton );
				sidebarElement.classList.remove( 'narrow', 'hidden' );
				annotationsUIs.switchTo( 'wideSidebar' );

				editorElement.classList.add( 'custom-sidebar-wide' );
				editorElement.classList.remove( 'custom-sidebar-narrow' );
			}

			function switchToWideSidebarAndInline() {
				markActiveButton( wideAndInlineButton );
				sidebarElement.classList.remove( 'narrow', 'hidden' );
				editorElement.classList.add( 'custom-sidebar-wide' );
				editorElement.classList.remove( 'custom-sidebar-narrow' );

				annotationsUIs.deactivateAll();
				annotationsUIs.activate( 'wideSidebar', annotation => annotation.type === 'comment' );
				annotationsUIs.activate( 'inline', annotation => annotation.type !== 'comment' );
			}

			editor.ui.view.listenTo( inlineButton, 'click', () => switchToInline() );
			editor.ui.view.listenTo( narrowButton, 'click', () => switchToNarrowSidebar() );
			editor.ui.view.listenTo( wideButton, 'click', () => switchToWideSidebar() );
			editor.ui.view.listenTo( wideAndInlineButton, 'click', () => switchToWideSidebarAndInline() );

			// Set wide sidebar as the default.
			switchToWideSidebar();

			return editor;
		} );
} );

watchdog.setDestructor( editor => editor.destroy() );

watchdog.create( document.querySelector( '.display-mode .editor' ), {
	initialData,
	extraPlugins: [ CommentsAndTrackChangesAdapter ],
	toolbar: {
		items: [
			'heading', '|', 'bold', 'italic', 'link', 'bulletedList', 'numberedList', 'insertImage', 'blockquote', '|',
			'undo', 'redo', '|', 'insertTable', '|', 'comment', 'commentsArchive', 'trackChanges'
		]
	},
	licenseKey: LICENSE_KEY,
	sidebar: {
		container: document.querySelector( '.display-mode .sidebar' )
	},
	image: {
		toolbar: [
			'imageStyle:inline', 'imageStyle:block', 'imageStyle:wrapText', '|',
			'imageTextAlternative', '|', 'comment'
		]
	},
	cloudServices: CS_CONFIG,
	ui: {
		viewportOffset: {
			top: window.getViewportTopOffsetConfig()
		}
	},
	comments: {
		editorConfig: {
			extraPlugins: ClassicEditor.builtinPlugins.filter( plugin => {
				return [ 'Bold', 'Italic', 'List', 'Autoformat' ].includes( plugin.pluginName );
			} )
		}
	},
	mention: {
		feeds: [
			{
				marker: '@',
				feed: [ '@Barney', '@Lily', '@Marry Ann', '@Marshall', '@Robin', '@Ted' ]
			}
		]
	}
} );
